import React, { lazy, useEffect, useState } from "react";
import { useParams } from "react-router-dom";

import { blogs } from "../Data/BlogData.js";
import Header from "../components/Templates/Header.js";
import BlogSidebar from "../components/Templates/BlogSidebar.js";

function getBlogData(id) {
  const data = blogs[id];
  return data;
}

export default function Blog() {
  const { blogId } = useParams();
  const [data, setData] = useState();
  const [BlogContent, setBlogContent] = useState();

  useEffect(() => {
    let res = getBlogData(blogId);
    if (res.related && res.related.length > 0 && res.related[0].key) {
      res.related = res.related.map((related) => getBlogData(related.key));
    }
    setData(res);
    document.title = res.title;

    try {
      const blogData = lazy(() => import(`../components/blogs/${blogId}.js`));
      setBlogContent(blogData);
    } catch (error) {
      console.error("Error fetching blog content:", error);
    }
  }, [blogId]);

  return (
    <>
      {data && (
        <Header
          title={data.title}
          subtitle={data.subtitle}
          image={data.image}
        />
      )}
      <div className="container-fluid black text-white px-5 py-3">
        <div className="bdr-top"></div>
        <div className="row">
          <div className="col-md-9 py-4 px-1">
            <div className="overflow-auto">
              <div className="container">{BlogContent && <BlogContent />}</div>
            </div>
          </div>
          {data && <BlogSidebar related={data.related} />}
        </div>
        <div className="bdr-top"></div>
      </div>
    </>
  );
}
