import React from "react";
import PropTypes from "prop-types";

export default function Header(props) {
  return (
    <div className="header-section text-center text-white">
      <div className="container">
        <div className="row">
          <div
            className={`col-md-6 col-sm-12 align-self-center ${
              props.main ? "my-5" : "px-4 py-3"
            }`}
          >
            <h1 className={props.main ? "display-1" : "title"}>
              {props.title ?? (
                <span>
                  Tech, Beyond <b>Boundaries</b>
                </span>
              )}
            </h1>
            <p className="lead subtitle">{props.subtitle}</p>
          </div>
          <div className="col align-self-center">
            <img
              className={`img-fluid ${props.blend && "blend-bottom img-head"}`}
              src={props.image}
              alt="header"
            />
          </div>
        </div>
      </div>
    </div>
  );
}

Header.defaultProps = {
  subtitle: "Your daily dose of tech!",
  image: "/assets/home_header_laptop.webp",
  blend: true,
};

Header.propTypes = {
  title: PropTypes.string,
  subtitle: PropTypes.string,
  image: PropTypes.string,
  buttonText: PropTypes.string,
};
