import React, { useState, useEffect } from "react";
import { blogs, recentPosts } from "../../Data/BlogData.js";
import Card from "./Card.js";

function loadLatestPosts() {
  let result = [];
  for (let i = 0; i < Math.min(recentPosts.length, 8); i++) {
    result.push(blogs[recentPosts[i]]);
  }
  return result;
}

export default function RecentPosts(props) {
  const [latestPosts, setLatestPosts] = useState([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    if (latestPosts.length === 0) {
      setLatestPosts(loadLatestPosts());
      setLoading(false);
    }
  }, [latestPosts]);

  return (
    <div className="white">
      <div className="container py-5" id="recent-posts">
        {props.header && (
          <h2 className="display-4 text-center mb-4">
            <b>
              <b>Recent Posts</b>
            </b>
          </h2>
        )}
        <div className="row" id="recent-posts-section">
          {loading && (
            <div className="container text-center" id="spinner">
              <div className="spinner-border" role="status">
                <span className="visually-hidden">Loading...</span>
              </div>
            </div>
          )}
          {!loading &&
            latestPosts.length > 0 &&
            latestPosts.map((post, index) => {
              if (index === 0) {
                return (
                  <div
                    className="col-lg-6 mb-4"
                    id="recent-posts-large"
                    key={post.title}
                  >
                    <Card
                      title={post.title}
                      image={post.image}
                      link={post.link}
                      subtitle={post.subtitle}
                      header={true}
                    />
                  </div>
                );
              }
              return (
                <div className="col-lg-3 col-6 mb-4" key={post.title}>
                  <Card
                    title={post.title}
                    image={post.image}
                    link={post.link}
                    subtitle={post.subtitle}
                  />
                </div>
              );
            })}
        </div>
      </div>
    </div>
  );
}

RecentPosts.defaultProps = {
  header: true,
};
