import React from "react";
import PropTypes from "prop-types";
import { Link } from "react-router-dom";

export default function Card(props) {
  return (
    <>
      <div className="lifted raised round-element h-100">
        <Link className="no-decor" to={props.link}>
          <div className="card black hover-effect round-element text-center text-white h-100">
            <img
              className="round-top-element pt-3 image-card card-img-top"
              src={props.image}
              alt={props.title}
            />
            <div className="card-body">
              {props.header ? (
                <>
                  <h3 className="card-title ">{props.title}</h3>
                  <p className="card-text subtitle normal-text">
                    {props.subtitle}
                  </p>
                </>
              ) : (
                <>
                  <p className="card-title lead m-0">{props.title}</p>
                  {/* {props.subtitle && (
                    <small className="subtitle normal-text m-0">
                      {props.subtitle}
                    </small>
                  )} */}
                </>
              )}
            </div>
          </div>
        </Link>
      </div>
    </>
  );
}

Card.defaultProps = {
  header: false,
  subtitle: "",
};

Card.propTypes = {
  title: PropTypes.string.isRequired,
  image: PropTypes.string.isRequired,
  link: PropTypes.string.isRequired,
  header: PropTypes.bool,
  subtitle: PropTypes.string,
};
